import React from "react";
import { InputField } from "./InputField";
import { CheckBox } from "./CheckBox";
import { Button } from "./Button";

export const Register = ({
  confirmed,
  values: { name, email, password, termsNPolicy },
  termsNPolicyHover,
  onSubmit,
  onMouseEnter,
  onMouseLeave,
  onChangeInput,
}) => {
  //const { name, email, password, termsNPolicy } = { values };
  return (
    <div className="gray-bg">
      <div className="middle-box text-center loginscreen   animated fadeInDown">
        <div>
          <div>
            <h1 className="logo-name">IN+</h1>
          </div>
          <h3>Register to IN+</h3>
          <p>Create account to see it in action.</p>
          <form className="m-t">
            {/*role="form" action="login.html" */}
            <InputField
              id="Name"
              name="name"
              type="text"
              placeholder="Name"
              value={name}
              onChangeInput={onChangeInput}
            />
            <InputField
              id="Email"
              name="email"
              type="email"
              placeholder="Email"
              value={email}
              onChangeInput={onChangeInput}
            />
            <InputField
              id="Password"
              type="password"
              name="password"
              placeholder="Password"
              value={password}
              onChangeInput={onChangeInput}
            />
            <CheckBox
              id="TermsNPolicy"
              name="termsNPolicy"
              value={termsNPolicy}
              label="Agree the terms and policy"
              hover={termsNPolicyHover}
              onChangeInput={onChangeInput}
              onMouseEnter={onMouseEnter}
              onMouseLeave={onMouseLeave}
            />
            <Button
              type="submit"
              className="btn btn-primary block full-width m-b"
              label="Register"
              onSubmit={onSubmit}
            />
            <p className="text-muted text-center">
              <small>Already have an account?</small>
            </p>
            <a className="btn btn-sm btn-white btn-block" href="login.html">
              Login
            </a>
          </form>
          <p className="m-t">
            <small>
              Inspinia we app framework base on Bootstrap 3 &copy; 2014
            </small>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Register;
